
.keypad {
  width:30rem;
  height:13.5rem;
  background: black;
  font-family:Fantasy;
  margin-left:52rem ;
  margin-top:-15rem;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    opacity: 0;
    z-index:10;
    transform: translateY(-30px);
    transition: opacity 0.5s ease, transform 0.8s ease;
}


.keypad.visible {
    opacity: 1; z-index:30;
    transform: translateY(0);
}



.coop2{margin-top:2rem; margin-left:6rem;
position:relative; z-index:10; width:15rem; text-align:center;
}


.otherbtn{width:2.2rem;
  height:2.4rem;
  font-size:1.1rem;
margin-left:0.5rem;}

.spacebtn{
  margin-left:0.5rem;
  margin-top:0.3rem;
  width:8.5rem;
  height:3.3rem;
  font-size:2rem;
}

.emobtn{
  width:3.2rem;
  height:2.5rem;
  font-size:1.1rem;
margin-left:0.8rem;
}

.keybtn{
  color:rgb(7, 7, 7);
  z-index:30;
  margin-top:8rem;
  margin-left:40rem;
  text-align: center;
  font-size:1.6rem;
  animation-name:btnbtn;
  animation-duration:1s;
  animation-iteration-count:infinite;
  animation-direction:alternate;
  animation-delay:0ms;
}


.upperbtn{margin-left:0.7%;}
.lowerbtn{margin-left:1%;}
.clearbtn{margin-left:1%;}
.copybtn{margin-left:1%;}
.backbtn{margin-top:-4.5rem; margin-left:96%;}

.converters{position: absolute;
margin-top:2.7rem; z-index:10;}

.textbox textarea{ 
  width:50rem ; z-index:20;
  height: 8rem;
  font-size:1rem;
  padding: 1.5rem;
  margin-top:-1.9rem;
  position:absolute;
box-sizing: border-box; 
vertical-align: top
 }

 .container h2{
  font-family:'Times New Roman';
  margin-top:2rem;
}

.sumary{
  margin-top:1rem;
  z-index:10;
}

.sumary h3{
  background-image:linear-gradient(to top, rgba(89, 90, 90, 0.747),rgba(190, 190, 82, 0.726));
  width:max-content; height:2.5rem;
}
.sumary h4{
  background-image:linear-gradient(to top, rgba(89, 90, 90, 0.747),rgba(190, 190, 82, 0.726));
  width:max-content; height:2rem;
}

.switchh{
  color:white;
  margin-top:-2rem;
  margin-left:77rem;
  font-size:1rem;
}

.form-check-input{margin-left:0.5rem;}

  .form-check-label{
   background-color: rgba(22, 22, 22, 0.781);
  width:9.2rem; text-align: center; }


  @keyframes btnbtn{
    0%{ background-image:linear-gradient(to bottom, rgba(80, 82, 81, 0.747),rgb(14, 13, 13));
      border-color: rgba(233, 233, 233, 0.658);
       color: white;
        transform:translate(0rem,2rem);
    }
    100%{
      background-image:linear-gradient(to bottom, rgb(253, 255, 250),rgb(88, 85, 85));
      border-color: rgba(255, 255, 255, 0.616);
      color: rgba(2, 2, 2, 0.884);
        transform:translate(0rem,6rem); }
    }



@media  only screen and (width <= 485px){
  .switchh{margin-left:40%; font-size:0.9rem; margin-top:-2rem;} .sumary{font-size:0.9rem; margin-top:-7rem;}
  .sumary h3{font-size:1rem; height:1.5rem;} .sumary h4{font-size:1rem; height:1.5rem;}
  .container h2{font-size:1.5rem; position:absolute; margin-top:1rem; margin-left:0.5%; z-index:20;}
  .coop2{display:none; visibility: hidden;} .form-check-input{margin-left:6%;}
  .coop2 button{display:none; visibility: hidden;} 
  .textbox textarea{width:20rem; height: 5.5rem; margin-top:3rem; margin-left:-0.5rem; position:absolute;}
  .upperbtn{width:7rem; height:2rem; font-size:0.6rem;margin-left:-1.5%; margin-top:2.5rem; padding:0%;}
  .lowerbtn{width:7rem; height:2rem; font-size:0.6rem;margin-left:0.5%; margin-top:2.4rem; padding:0%;}
  .copybtn{width:6rem; height:2rem; font-size:0.6rem;margin-left:-57%; margin-top:4.7rem; padding:0%; position: fixed;}
  .clearbtn{width:4rem; height:2rem; font-size:0.6rem;margin-left:-30%; margin-top:4.7rem; padding:0%; position:fixed;}
  .backbtn{width:4rem; height:2rem; font-size:0.6rem;margin-left:6%; margin-top:2.3rem; padding:0%; position: fixed;}
  .keybtn{margin-left:70%; margin-top:7rem; font-size:1.1rem;}
  
  .otherbtn{font-size:0.7rem; width:0.5rem; height:1.7rem; margin:0.6%;}
  .spacebtn{font-size:0.9rem; width:5rem; height:2.1rem; margin-top:0%; margin-left:2.2%;}
  .emobtn{font-size:0.8rem; width:2.4rem; height:2rem; margin:0.5%; margin-left:2%;}
  .keypad{width:21rem; height:10rem; margin-left:-1%; margin-top:6rem; z-index:30;}
  html body{margin: 0%; padding:0%; width:100%; height:100%; overflow: hidden;}
}


.App-link {
  color: #61dafb;
}

